export default {
    props: {
        field_name: {
            type: '',
            default() {
                return [];
            }
        },
        default_value: {
            default: ''
        }
        ,
        options: {
            type: '',
            default() {
                return [];
            }
        },
        level: {
            default: 0
        }
        ,
        name_key: {
            default: 'name'
        }
        ,
        single_column: {
            type: '',
            default() {
                return [];
            }
        },
        group: {
            type: String,
            default: ''
        },
        unit: {
            type: String,
            default: ''
        },
        mini_type_prop: {
            type: String,
            default: ''
        },
        prop_placeholder: {
            type: String,
            default: ''
        },

        max_amount: {
            type: Number,
            default: 0
        },
        show_extra: {
            default: false

        }
    },
    data: function () {
        return {
            form_type: false,
            default_loc_value: '',
            mini_type: '',
            placeholder: ''
            , extra_txt: ''
        }
    },

    methods: {
        open_location: function () {
            uni.chooseLocation({
                success: (res) => {
                    //this.toast('位置名称：' + JSON.stringify(res));
                    //console.log('详细地址：' + res.address);
                    //console.log('纬度：' + res.latitude);
                    //console.log('经度：' + res.longitude);
                    this.default_loc_value = res.address;
                    let arr = [this.field_name, this.default_loc_value];
                    this.out_put(arr);
                    arr = [this.lat, res.latitude];
                    this.out_put(arr);
                    arr = [this.lng, res.longitude];
                    this.out_put(arr);

                    this.extra_txt = "坐标:" + res.longitude + "," + res.latitude;
                }
            });
        },
        out_put: function (arr) {
            this.$emit('updated', arr)
        },
        inputs_change: function (e) {
            let arr = [this.field_name, e.detail.value];
            this.out_put(arr)
        },
    },
    created: function () {
        this.placeholder = this.prop_placeholder;
        this.default_loc_value = this.default_value;
    }
}
